export default {
  data() {
    return {
      name: '',
      email: '',
      authCode: '', //     
      inputCode: '', //   
      password: '',
      confirmPassword: '',
      nickname: '',
      isEmailValid: true,
      isPasswordValid: true,
      isCodeValid: true,
      emailSent: false,
      canSubmit: false
    }
  },
  methods: {
    validateEmail() {
      // @dgu.ac.kr  
      const emailRegex = /^[a-zA-Z0-9._%+-]+@dgu\.ac\.kr$/
      this.isEmailValid = emailRegex.test(this.email)
    },

    validatePassword() {
      //  8, , ,   2   
      const passwordRegex =
        /^(?=.*[A-Za-z])(?=.*\d|.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{8,}$/
      this.isPasswordValid = passwordRegex.test(this.password)
    },

    sendVerificationEmail() {
      this.validateEmail() //   
      if (this.isEmailValid) {
        //   
        fetch(`${process.env.VUE_APP_BE_API_URL}/api/users/signup/send-email`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ email: this.email })
        })
          .then((response) => response.text()) //   
          .then((data) => {
            if (data.includes('   .')) {
              this.emailSent = true //      
              alert(data) // "   ."  
            } else {
              alert('  .  .')
            }
          })
          .catch((error) => {
            console.error('Failed to send verification email:', error)
          })
      } else {
        alert(' @dgu.ac.kr  .')
      }
    },

    verifyCode() {
      //       
      fetch(`${process.env.VUE_APP_BE_API_URL}/api/users/signup/verify-code`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          email: this.email,
          code: this.authCode //    
        })
      })
        .then((response) => response.text()) //   
        .then((data) => {
          if (data === '  .') {
            this.isCodeValid = true
            alert(data) // "  ."  
          } else {
            this.isCodeValid = false
            alert('  .') //   
          }
        })
        .catch((error) => {
          console.error('Failed to verify code:', error)
          alert('    .')
        })
    },

    onSubmit() {
      this.validatePassword() //   
      if (this.isPasswordValid && this.canSubmit && this.isCodeValid) {
        //  
        fetch(`${process.env.VUE_APP_BE_API_URL}/api/users/signup`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            email: this.email,
            authCode: this.authCode, //    
            password: this.password,
            name: this.name,
            nickname: this.nickname
          })
        })
          .then((response) => response.text()) //   
          .then((data) => {
            if (data === ' ') {
              alert(data) // " ."  
              this.$router.push('/login') //     
            } else {
              alert(' .  .')
            }
          })
          .catch((error) => {
            console.error('Failed to register:', error)
            alert('   .')
          })
      } else {
        if (!this.isPasswordValid) {
          alert(
            '  8, , ,   2  .'
          )
        }
        if (!this.isCodeValid) {
          alert('  .')
        }
      }
    }
  }
}
