<template>
  <div class="container">
    <!--  -->
    <MainHeader />

    <!--   -->
    <div class="activity-info">
      <div class="activity-name-container">
        <label for="activity-name"><h2></h2></label>
        <input type="text" id="activity-name" v-model="activityName" placeholder=" " />
      </div>

      <div class="activity-date-container">
        <label for="activity-date"> <h2></h2> </label>
        <input type="date" id="activity-date" v-model="activityDate" />
      </div>
    </div>

    <!--   -->
    <div class="category-box">
      <div class="category">
        <label @click="toggleDropdown" class="category-label" >
          <h2> </h2>
          <span v-if="selectedTags.length">
            <span class="tag-badge" v-for="tag in selectedTags" :key="tag">
              {{ tag }}
            </span>
          </span>
        </label>

        <div v-show="isDropdownOpen" class="tag-container">
          <button
            v-for="tag in tags"
            :key="tag"
            @click="toggleTag(tag)"
            :class="{ active: selectedTags.includes(tag) }"
          >
            {{ tag }}
          </button>
        </div>
      </div>
    </div>

    <!--  (STAR ) -->
    <div class="experience-container">
      <div class="experience-box star-box">
        <h2></h2>
        <div class="star-section">
          <h3>Situation</h3>
          <textarea v-model="star.situation" placeholder="/ " @input="autoResize($event)"></textarea>
        </div>

        <div class="star-section">
          <h3>Task</h3>
          <textarea v-model="star.task" placeholder="/ " @input="autoResize($event)"></textarea>
        </div>

        <div class="star-section">
          <h3>Action</h3>
          <textarea v-model="star.action" placeholder="// " @input="autoResize($event)"></textarea>
        </div>

        <div class="star-section">
          <h3>Result</h3>
          <textarea v-model="star.result" placeholder=" " @input="autoResize($event)"></textarea>
        </div>
      </div>
    </div>

    <!--  PMI -->
    <div class="pmi-container">
      <div class="pmi-box">
        <h2> PMI 
          <span 
            class="tooltip-icon" 
            @mouseover="tooltipVisible = true" 
            @mouseleave="tooltipVisible = false"
          ></span>
          
          <div v-if="tooltipVisible" class="tooltip">
                ,  ,    
          </div>
        </h2>

        <div class="pmi-section">
          <h3>Plus</h3>
          <textarea v-model="pmi.plus" placeholder="    " @input="autoResize($event)"></textarea>
        </div>

        <div class="pmi-section">
          <h3>Minus</h3>
          <textarea v-model="pmi.minus" placeholder="    " @input="autoResize($event)"></textarea>
        </div>

        <div class="pmi-section">
          <h3>Interesting</h3>
          <textarea v-model="pmi.interesting" placeholder="    " @input="autoResize($event)"></textarea>
        </div>
      </div>
    </div>

    <!--   -->
    <button @click="saveData" class="save-button"></button>
    <MainFooter />
  </div>
</template>

<script>
import MainHeader from '../../components/layout/Header.vue'
import MainFooter from '../../components/layout/Footer.vue'

export default {
  components: {
    MainHeader,
    MainFooter
  },
  data() {
    return {
      activityName: '',
      activityDate: '',
      tags: [
        '', '', '', '', '', '', '', '',
        '', '', '', '/', '', '', '',
        '', '', ''
      ],
      selectedTags: [''], 
      isDropdownOpen: false,
      star: {
        situation: '',
        task: '',
        action: '',
        result: ''
      },
      pmi: {
        plus: '',
        minus: '',
        interesting: ''
      },
      tooltipVisible: false
    };
  },

  computed: {
    isFormComplete() {
      return (
        this.activityName &&
        this.activityDate &&
        this.selectedTags.length > 0 &&
        Object.values(this.star).every((field) => field) &&
        Object.values(this.pmi).every((field) => field)
      );
    }
  },

  methods: {
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },
    toggleTag(tag) {
      const index = this.selectedTags.indexOf(tag);
      if (index > -1) {
        this.selectedTags.splice(index, 1);
      } else {
        this.selectedTags.push(tag);
      }
    },
    autoResize(event) {
      const textarea = event.target;
      textarea.style.height = 'auto';
      textarea.style.height = `${textarea.scrollHeight}px`;
    },

    
    saveData() {
      if (!this.isFormComplete) {
        alert('  .');
        return;
      }

    const newPortfolio = {
      title: this.activityName,
      createdDate: this.activityDate,
      tags: this.selectedTags,
      star: {...this.star},
      pmi: {...this.pmi}
    };

        // Vuex   ,   
        this.$store.dispatch('addPortfolio', newPortfolio).then(() => {
      alert(' !');
      this.$router.push('/akopolio/main'); //    
    });

    this.resetForm(); //   ( )
  },
  resetForm() {
      this.activityName = '';
      this.activityDate = '';
      this.selectedTags = ['']; //   
      this.star = { situation: '', task: '', action: '', result: '' };
      this.pmi = { plus: '', minus: '', interesting: '' };
    }
  }
};

</script>

<style scoped>
.container {
  width: 395px;
  max-width: 500px;
  margin: 4rem auto;
  padding: 20px;  
  background-color: #ffe8d1;
  min-height: calc(100vh - 120px); /*       */
}

.header {
  text-align: center;
}

.activity-info,
.category-box,
.experience-container,
.pmi-container {
  margin-bottom: 20px;
}

.activity-name-container {
  margin-bottom: 20px; 
}


input[type="text"],
input[type="date"],
textarea {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  background-color: #fff3e6;
  border-radius: 5px;
  resize: none;
  font-size: 13px;
}

.star-box,
.pmi-box {
  background-color: #fff3e6;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.activity-info{
  background-color: #fff3e6;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.tag-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  font-size: 12px;
}

button {
  display: inline-block;
  margin: 3px;
  padding: 8px;
  border-radius: 40px;
  background-color: white;
  transition: background-color 0.3s;
}

button.active {
  background-color: #F6B87A;
  color: white;
}

/*    */
.category-box {
  background-color: #fff3e6;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.category-label {
  display: flex; 
  align-items: center; 
}

.tag-badge {
  display: inline-block;
  margin-left: 10px;
  background-color: #F6B87A;
  color: white;
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 13px;
}

.save-button {
  width: 100%;
  padding: 15px;
  background-color: #F6B87A;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  ;
}

.save-button:hover {
  background-color: #f4c08c;
}

h3 {
  font-size: 15px; 
  color: #FF7F00;
  margin-top: 10px;
}

h2 {
  margin: 0;
  font-size: 15px;  
}

label {
  font-size: 15px;  
}

.tooltip {
  position: absolute;
  background-color: rgba(51, 51, 51, 0.9);
  color: white;
  padding: 8px;
  border-radius: 5px;
  z-index: 1000; 
  white-space: nowrap; 
}


</style>